import { Box, makeStyles, Slide, Typography } from "@material-ui/core";
import React, { useState } from "react";
import { Annotation } from "../../Data/Annotations";
import { ItemData } from "../../Data/Items";
import { AnnotationContainer } from "./AnnotationContainer";

const useStyles = makeStyles({
  itemContainer: {
    margin: 15,
    border: "1px solid black",
    borderRadius: 10,
    overflow: "auto",
    overflowX: "hidden",
    height: "calc(100% - 30px)",
    width: "80%",
    "&::-webkit-scrollbar": {
      height: 10,
    },
    "&::-webkit-scrollbar-track": {
      "-webkit-box-shadow": "inset 0 0 6px rgba(0,0,0,0.3)",
      "-webkit-border-top-right-radius": 10,
      "-webkit-border-bottom-right-radius": 10,
      borderTopRightRadius: 10,
      borderBottomRightRadius: 10,
      marginLeft: 10,
      marginRight: 10,
    },
    "&::-webkit-scrollbar-thumb": {
      "-webkit-border-top-right-radius": 10,
      "-webkit-border-bottom-right-radius": 10,
      borderTopRightRadius: 10,
      borderBottomRightRadius: 10,
      background: "#402B56",
      "-webkit-box-shadow": "inset 0 0 6px rgba(0,0,0,0.5)",
    },
    "&.correct": {
      "&::-webkit-scrollbar-thumb": {
        background: "#dcffdc",
      },
      border: "1px solid #8ADDAE",
    },
    "&.incorrect": {
      "&::-webkit-scrollbar-thumb": {
        background: "#f8e7e6",
      },
      border: "1px solid #CB7D75",
    },
  },
  contentContainer: {
    padding: 15,
  },
  rationaleContainer: {
    padding: 15,
    "&.correct": {
      borderTop: "1px solid #8ADDAE",
      background: "#dcffdc",
    },
    "&.incorrect": {
      borderTop: "1px solid #CB7D75",
      background: "#f8e7e6",
    },
  },
  boldedRationale: {
    fontWeight: "bold",
    "& p": {
      fontWeight: "bold",
    },
  },
});

export interface ContentContainerProperties {
  item: ItemData;
  direction?: string;
  annotationVisibility: boolean;
}

export const ContentContainer = ({
  item,
  direction,
  annotationVisibility,
}: ContentContainerProperties) => {
  const classes = useStyles();

  const [annotations, setAnnotations] = useState<Array<Annotation>>([]);

  const addAnnotation = (annotation: Annotation) =>
    setAnnotations([...annotations, annotation]);
  const updateAnnotation = (index: number, note: string) =>
    setAnnotations([
      ...annotations.slice(0, index),
      Object.assign({}, annotations[index], { note }),
      ...annotations.slice(index + 1),
    ]);
  const removeAnnotation = (index: number) =>
    setAnnotations([
      ...annotations.slice(0, index),
      ...annotations.slice(index + 1),
    ]);

  return (
    <Box
      className={`${classes.itemContainer} ${
        item.correct ? "correct" : "incorrect"
      }`}
    >
      <Slide
        direction={
          direction === "forward"
            ? "left"
            : direction === "backward"
            ? "right"
            : "down"
        }
        in={true}
        timeout={450}
        key={item.number}
      >
        <Box>
          <AnnotationContainer
            displayAnnotations={annotationVisibility}
            annotations={annotations}
            addAnnotation={addAnnotation}
            updateAnnotation={updateAnnotation}
            removeAnnotation={removeAnnotation}
          >
            <Box
              className={classes.contentContainer}
              dangerouslySetInnerHTML={{ __html: item.content }}
            ></Box>
            <Box
              className={`${classes.rationaleContainer} ${
                item.correct ? "correct" : "incorrect"
              }`}
            >
              <Typography>
                <b>Solution:</b> The correct answer is{" "}
                <b>{item.correctAnswer}</b>.
              </Typography>
              <ol type="A">
                {item.answerRationales.map((rationale, index) => (
                  <li
                    key={index}
                    className={
                      item.correctAnswer.charCodeAt(0) ===
                      "A".charCodeAt(0) + index
                        ? classes.boldedRationale
                        : ""
                    }
                  >
                    <Typography>{rationale}</Typography>
                  </li>
                ))}
              </ol>
            </Box>
          </AnnotationContainer>
        </Box>
      </Slide>
    </Box>
  );
};
