import { Box, Dialog, makeStyles } from "@material-ui/core";
import React, { useState } from "react";
import { ItemResults } from "../../Data/Items";
import { ReviewHeader } from "./ReviewHeader";
import { ContentContainer } from "./ContentContainer";
import { InfoContainer } from "./InfoContainer";

const useStyles = makeStyles({
  root: {
    top: "5% !important",
    left: "5% !important",
    bottom: "5% !important",
    right: "5% !important",
  },
  fullSize: {
    height: "100%",
    maxHeight: "100%",
    width: "100%",
    maxWidth: "100%",
    overflow: "hidden",
  },
  outerContainer: {
    display: "flex",
    width: "100%",
    height: "calc(100% - 64px)",
  },
  innerContainer: {
    display: "flex",
    flexDirection: "row",
  },
});

export interface ReviewDialogProperties {
  open: boolean;
  onClose: () => void;
  itemNumber: number | undefined;
  section: string;
  canNext: boolean;
  canPrevious: boolean;
  onNext: () => void;
  onPrevious: () => void;
  onFirst: () => void;
  onLast: () => void;
  title: string;
}

export const ReviewDialog = ({
  open,
  onClose,
  itemNumber,
  section,
  canNext,
  canPrevious,
  onNext,
  onPrevious,
  onFirst,
  onLast,
  title,
}: ReviewDialogProperties) => {
  const classes = useStyles();

  const item = ItemResults.filter(
    (i) => i.section === section && i.number === itemNumber
  )[0];

  const [direction, setDirection] = useState<
    "forward" | "backward" | undefined
  >();

  const [annotations, setAnnotations] = useState<boolean>(false);

  const goLast = () => {
    setDirection("forward");
    onLast();
  };

  const goForward = () => {
    setDirection("forward");
    onNext();
  };

  const goBackward = () => {
    setDirection("backward");
    onPrevious();
  };

  const goFirst = () => {
    setDirection("backward");
    onFirst();
  };

  const close = () => {
    setDirection(undefined);
    onClose();
  };

  if (!item) return <></>;

  return (
    <Dialog
      open={open}
      className={classes.root}
      classes={{ container: classes.fullSize, paper: classes.fullSize }}
    >
      <ReviewHeader
        title={title}
        close={close}
        canNext={canNext}
        canPrevious={canPrevious}
        goFirst={goFirst}
        goBackward={goBackward}
        goForward={goForward}
        goLast={goLast}
      />
      <Box className={classes.outerContainer}>
        <Box className={classes.innerContainer}>
          <ContentContainer
            key={item.number}
            item={item}
            direction={direction}
            annotationVisibility={annotations}
          />
          <InfoContainer
            item={item}
            annotationVisibility={annotations}
            toggleAnnotationVisibility={() => setAnnotations(!annotations)}
          />
        </Box>
      </Box>
    </Dialog>
  );
};
