export const getSafeRanges = (
  dangerous: Range | undefined,
  elements: Array<Node> = []
): Array<Range> => {
  if (!dangerous) return [];
  let singleElement = dangerous.startContainer === dangerous.endContainer;

  let startRange =
    elements.indexOf(dangerous.startContainer) >= 0
      ? []
      : getIndividualLetterRanges(
          dangerous.startContainer,
          dangerous.startOffset,
          singleElement ? dangerous.endOffset : undefined
        );

  if (singleElement) return startRange;

  let endRange =
    elements.indexOf(dangerous.endContainer) >= 0
      ? []
      : getIndividualLetterRanges(
          dangerous.endContainer,
          0,
          dangerous.endOffset
        );

  var nextStart = getNextElement(dangerous.startContainer);

  var nextEnd = getPreviousElement(dangerous.endContainer);

  if (!nextStart || !nextEnd) return [...startRange, ...endRange];

  var middleRange = document.createRange();
  middleRange.setStart(nextStart, 0);
  middleRange.setEnd(nextEnd, 0);
  return [
    ...startRange,
    ...getSafeRanges(middleRange, [
      ...elements,
      dangerous.startContainer,
      dangerous.endContainer,
    ]),
    ...endRange,
  ];
};

const getNextElement = (element: Node): Node | null => {
  let currentElement: Node | HTMLElement | null = element;

  while (currentElement && !currentElement.nextSibling) {
    currentElement = currentElement?.parentElement;
  }

  if (!currentElement) return null;
  currentElement = currentElement.nextSibling;

  while (currentElement?.hasChildNodes()) {
    currentElement = currentElement.firstChild;
  }

  return currentElement;
};

const getPreviousElement = (element: Node): Node | null => {
  let currentElement: Node | HTMLElement | null = element;

  while (currentElement && !currentElement.previousSibling) {
    currentElement = currentElement?.parentElement;
  }

  if (!currentElement) return null;
  currentElement = currentElement.previousSibling;

  while (currentElement?.hasChildNodes()) {
    currentElement = currentElement.lastChild;
  }

  return currentElement;
};

const getIndividualLetterRanges = (
  element: Node,
  start?: number,
  end?: number
) => {
  let ranges: Array<Range> = [];

  if (element.nodeType === Node.TEXT_NODE) {
    let elementSize = (element as any).length;
    for (
      var subIndex = start || 0;
      subIndex < (end || elementSize);
      subIndex++
    ) {
      try {
        let selection = document.createRange();
        selection.setStart(element, subIndex);
        selection.setEnd(element, subIndex + 1);
        ranges.push(selection);
      } catch (e) {}
    }
  } else if (element.hasChildNodes()) {
    for (
      let nodeIndex = 0;
      nodeIndex < element.childNodes.length;
      nodeIndex++
    ) {
      let childNode = element.childNodes.item(nodeIndex);
      let childNodeRanges = getIndividualLetterRanges(
        childNode,
        nodeIndex === 0 ? start : undefined,
        nodeIndex === element.childNodes.length - 1 ? end : undefined
      );
      ranges.push(...childNodeRanges);
    }
  }

  return ranges;
};
