import {
  Box,
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  Fade,
  IconButton,
  makeStyles,
  TextField,
  Typography,
} from "@material-ui/core";
import React, { FunctionComponent, useEffect, useRef, useState } from "react";
import NoteAddIcon from "@material-ui/icons/NoteAdd";
import { Annotation, AnnotationRange } from "../../Data/Annotations";
import CloseIcon from "@material-ui/icons/Close";
import EditIcon from "@material-ui/icons/Edit";
import { getSafeRanges } from "../../Utils/Selection";

const useStyles = makeStyles({
  root: {
    position: "relative",
    "& svg": {
      position: "absolute",
      pointerEvents: "none",
    },
  },
  addNote: {
    position: "absolute",
    background: "white",
    border: "1px solid #F8B332",
    padding: 20,
    zIndex: 101,
    "&:hover": {
      background: "#F8B332",
      border: "1px solid black",
    },
  },
  removeAnnotation: {
    position: "absolute",
    background: "white",
    border: "1px solid #F8B332",
    zIndex: 100,
    "&:hover": {
      background: "#F8B332",
      border: "1px solid black",
    },
  },
  annotationDialog: {
    minWidth: 500,
  },
  annotationContent: {
    height: "100%",
    width: "100%",
  },
  annotationHighlight: {
    pointerEvents: "all",
  },
  annotationNote: {
    position: "absolute",
    border: "1px solid black",
    borderRadius: 5,
    padding: 5,
    background: "#fde6bb",
    zIndex: 200,
  },
});

export interface FunctionComponentProperties {
  displayAnnotations: boolean;
  annotations: Array<Annotation>;
  addAnnotation: (annotation: Annotation) => void;
  updateAnnotation: (index: number, newNote: string) => void;
  removeAnnotation: (index: number) => void;
}

export const AnnotationContainer: FunctionComponent<FunctionComponentProperties> = ({
  displayAnnotations,
  annotations,
  addAnnotation,
  updateAnnotation,
  removeAnnotation,
  children,
}) => {
  const classes = useStyles();
  const outerRef = useRef<HTMLDivElement>(null);
  const [newRange, setNewRange] = useState<AnnotationRange | undefined>();
  const [openAnnotation, setOpenAnnotation] = useState<
    number | AnnotationRange | undefined
  >();
  const [editText, setEditText] = useState<string>("");
  const [annotationHover, setAnnotationHover] = useState<number | undefined>();

  const getOuterStationaryRect = () =>
    outerRef.current?.parentElement?.getBoundingClientRect();

  const onNewAnnotationClicked = () => {
    setEditText("");
    setOpenAnnotation(newRange);
  };

  const onEditAnnotationClicked = (index: number) => {
    setEditText(annotations[index].note);
    setOpenAnnotation(index);
  };

  const saveAnnotation = (note: string) => {
    if (openAnnotation === undefined) return;
    if (typeof openAnnotation === "object") {
      addAnnotation({ range: openAnnotation as AnnotationRange, note });
      setNewRange(undefined);
      if (window.getSelection()?.empty) window.getSelection()?.empty();
      if (window.getSelection()?.removeAllRanges)
        window.getSelection()?.removeAllRanges();
    } else {
      updateAnnotation(openAnnotation as number, note);
    }
    setOpenAnnotation(undefined);
  };

  const cancelAnnotation = () => {
    setOpenAnnotation(undefined);
  };

  useEffect(() => {
    const currentRef = outerRef.current;
    const onPointerUp = () => {
      setTimeout(() => {
        if (!displayAnnotations) return;
        if (window.getSelection()?.toString().length)
          setNewRange({
            overallRange: window.getSelection()!.getRangeAt(0),
            highlightRanges: getSafeRanges(
              window.getSelection()?.getRangeAt(0)
            ),
          });
        else setNewRange(undefined);
      }, 50);
    };

    currentRef?.addEventListener("pointerup", onPointerUp);

    return () => {
      currentRef?.removeEventListener("pointerup", onPointerUp);
    };
  }, [setNewRange, outerRef, openAnnotation, displayAnnotations]);

  return (
    <div className={classes.root} ref={outerRef}>
      {displayAnnotations && (
        <>
          <Dialog
            open={openAnnotation !== undefined}
            classes={{ paper: classes.annotationDialog }}
          >
            <DialogTitle>{`${
              openAnnotation === -1 ? "New" : "Edit"
            } Annotation Text`}</DialogTitle>
            <DialogContent>
              <TextField
                className={classes.annotationContent}
                autoFocus
                multiline
                value={editText}
                onChange={(event) => setEditText(event.target.value)}
              ></TextField>
            </DialogContent>
            <DialogActions>
              <Button onClick={() => cancelAnnotation()} color="secondary">
                Cancel
              </Button>
              <Button onClick={() => saveAnnotation(editText)} color="primary">
                OK
              </Button>
            </DialogActions>
          </Dialog>
          {newRange && (
            <IconButton
              onClick={() => onNewAnnotationClicked()}
              className={classes.addNote}
              style={{
                left:
                  newRange.overallRange?.getBoundingClientRect().right -
                  (getOuterStationaryRect()?.x || 0) -
                  20,
                top:
                  newRange.overallRange?.getBoundingClientRect().bottom -
                  (getOuterStationaryRect()?.y || 0) +
                  10,
              }}
            >
              <NoteAddIcon />
            </IconButton>
          )}
          {annotations.map((annotation, index) => (
            <div key={`annotation-${index}`}>
              <IconButton
                onClick={() => removeAnnotation(index)}
                className={classes.removeAnnotation}
                style={{
                  left:
                    annotation.range.overallRange.getBoundingClientRect()
                      .right -
                    (getOuterStationaryRect()?.x || 0) -
                    10,
                  top:
                    annotation.range.overallRange.getBoundingClientRect().top -
                    (getOuterStationaryRect()?.y || 0) -
                    20,
                }}
              >
                <CloseIcon />
              </IconButton>
              <IconButton
                onClick={() => onEditAnnotationClicked(index)}
                className={classes.removeAnnotation}
                style={{
                  left:
                    annotation.range.overallRange.getBoundingClientRect()
                      .right -
                    (getOuterStationaryRect()?.x || 0) -
                    40,
                  top:
                    annotation.range.overallRange.getBoundingClientRect().top -
                    (getOuterStationaryRect()?.y || 0) -
                    20,
                }}
              >
                <EditIcon />
              </IconButton>
              <Fade
                in={annotationHover === index && annotation.note.length > 0}
              >
                <Box
                  className={classes.annotationNote}
                  style={{
                    left:
                      annotation.range.overallRange.getBoundingClientRect()
                        .left +
                      annotation.range.overallRange.getBoundingClientRect()
                        .width /
                        2 -
                      (getOuterStationaryRect()?.x || 0),
                    top:
                      annotation.range.overallRange.getBoundingClientRect()
                        .top +
                      annotation.range.overallRange.getBoundingClientRect()
                        .height /
                        2 -
                      (getOuterStationaryRect()?.y || 0),
                  }}
                >
                  <Typography variant="subtitle2">{annotation.note}</Typography>
                </Box>
              </Fade>
            </div>
          ))}
          <svg height="100%" width="100%">
            <g
              transform={`translate(${0 - (getOuterStationaryRect()?.x || 0)},${
                0 - (getOuterStationaryRect()?.y || 0)
              })`}
            >
              {newRange &&
                newRange.highlightRanges.map((range, index) => (
                  <rect
                    key={`new-range-${index}`}
                    x={range?.getBoundingClientRect().x || 0}
                    y={(range?.getBoundingClientRect().y || 2) - 2}
                    height={range?.getBoundingClientRect().height + 4}
                    width={range?.getBoundingClientRect().width}
                    fill="#f8b33254"
                  />
                ))}
              {openAnnotation &&
                typeof openAnnotation === "object" &&
                openAnnotation.highlightRanges.map((range, index) => (
                  <rect
                    key={`open-annotation-range-${index}`}
                    x={range?.getBoundingClientRect().x || 0}
                    y={(range?.getBoundingClientRect().y || 2) - 2}
                    height={range?.getBoundingClientRect().height + 4}
                    width={range?.getBoundingClientRect().width}
                    fill="#f8b33254"
                  />
                ))}
              {annotations.map((annotation, index) => (
                <g key={`annotation-${index}`}>
                  {annotation.range.highlightRanges.map((range, rangeIndex) => (
                    <rect
                      key={`annotation-highlight-${rangeIndex}`}
                      x={range.getBoundingClientRect().x}
                      y={range.getBoundingClientRect().y - 2}
                      height={range.getBoundingClientRect().height + 4}
                      width={range.getBoundingClientRect().width}
                      fill="#f8b33254"
                      className={classes.annotationHighlight}
                      onMouseOver={() => setAnnotationHover(index)}
                      onMouseLeave={() => setAnnotationHover(undefined)}
                    />
                  ))}
                </g>
              ))}
            </g>
          </svg>
        </>
      )}
      {children}
    </div>
  );
};
