import {
  AppBar,
  Box,
  IconButton,
  makeStyles,
  Toolbar,
  Typography,
} from "@material-ui/core";
import React from "react";
import NavigateNextIcon from "@material-ui/icons/NavigateNext";
import NavigateBeforeIcon from "@material-ui/icons/NavigateBefore";
import FirstPageIcon from "@material-ui/icons/FirstPage";
import LastPageIcon from "@material-ui/icons/LastPage";
import CloseIcon from "@material-ui/icons/Close";

const useStyles = makeStyles({
  appBar: {
    background: "#402B56",
    position: "relative",
  },
  navButtonContainer: {
    margin: "auto",
  },
  title: {
    marginLeft: 10,
    marginRight: 10,
    verticalAlign: "middle",
  },
  navButton: {
    border: "1px solid #F8B332",
    color: "#F8B332",
    borderRadius: 0,
    paddingTop: 0,
    paddingBottom: 0,
    "&.first": {
      borderTopLeftRadius: 10,
      borderBottomLeftRadius: 10,
      borderTopRightRadius: 0,
      borderBottomRightRadius: 0,
    },
    "&.last": {
      borderTopLeftRadius: 0,
      borderBottomLeftRadius: 0,
      borderTopRightRadius: 10,
      borderBottomRightRadius: 10,
    },
    "&.Mui-disabled": {
      border: "1px solid gray",
      color: "gray",
    },
  },
});

export interface ReviewHeaderProperties {
  title: string;
  close: () => void;
  canPrevious: boolean;
  canNext: boolean;
  goFirst: () => void;
  goBackward: () => void;
  goForward: () => void;
  goLast: () => void;
}

export const ReviewHeader = ({
  title,
  close,
  canPrevious,
  canNext,
  goFirst,
  goBackward,
  goForward,
  goLast,
}: ReviewHeaderProperties) => {
  const classes = useStyles();

  return (
    <AppBar className={classes.appBar} position="fixed">
      <Toolbar>
        <IconButton
          edge="start"
          color="inherit"
          onClick={close}
          aria-label="close"
        >
          <CloseIcon />
        </IconButton>
        <Box className={classes.navButtonContainer}>
          <IconButton
            className={`${classes.navButton} first`}
            color="inherit"
            disabled={!canPrevious}
            onClick={goFirst}
          >
            <FirstPageIcon />
          </IconButton>
          <IconButton
            className={classes.navButton}
            color="inherit"
            disabled={!canPrevious}
            onClick={goBackward}
          >
            <NavigateBeforeIcon />
          </IconButton>
          <Typography display="inline" variant="h6" className={classes.title}>
            {title}
          </Typography>
          <IconButton
            className={classes.navButton}
            color="inherit"
            disabled={!canNext}
            onClick={goForward}
          >
            <NavigateNextIcon />
          </IconButton>
          <IconButton
            className={`${classes.navButton} last`}
            color="inherit"
            disabled={!canNext}
            onClick={goLast}
          >
            <LastPageIcon />
          </IconButton>
        </Box>
      </Toolbar>
    </AppBar>
  );
};
